<template>
<div  style="margin-right:10px;margin-left:15px;">
    <div v-for="(src,id) in backgroundList" :key="id">
        <div class="row" style="margin-top:8px;">
              <div  class="col-xs-6 col-md-6" :id="src.divid1"  @click="pickup(src.id1)">
                <a href="#" class="thumbnail" :style="src.style1">
                   <img style="height:128px;" :src="src.src1" alt="...">
                </a>
              </div>
              <div class="col-xs-6 col-md-6" :id="src.divid2"  @click="pickup(src.id2)">
                <a href="#" class="thumbnail" :style="src.style2">
                    <img style="height:128px;" :src="src.src2" alt="...">
                </a>
              </div>
        </div>
    </div>
    <hr>
    <div class="q-row">
        <span class="option-lb">上边框.    </span>
        <input class="option" id="mtop" type="number" @change="changeTop(mtop.number,mtop.color)" v-model="mtop.number" />
        <input class="option" style="width: 8%;padding: 0px;margin-left: 10px;vertical-align: middle;margin-top: -2px;"  type="color"  @change="changeTop(mtop.number,mtop.color)" v-model="mtop.color" />
    </div>
    <div class="q-row">
        <span class="option-lb">下边框.    </span>
        <input class="option" type="number" @change="changeBottom(mbottom.number,mbottom.color)" v-model="mbottom.number" />
        <input class="option" style="width: 8%;padding: 0px;margin-left: 10px;vertical-align: middle;margin-top: -2px;"  type="color"  @change="changeBottom(mbottom.number,mbottom.color)" v-model="mbottom.color" />
    </div>
    <div class="q-row">
        <span class="option-lb">左边框.    </span>
        <input class="option" type="number" @change="changeLeft(mleft.number,mleft.color)" v-model="mleft.number" />
        <input class="option" style="width: 8%;padding: 0px;margin-left: 10px;vertical-align: middle;margin-top: -2px;"  type="color"  @change="changeLeft(mleft.number,mleft.color)" v-model="mleft.color" />
    </div>
    <div class="q-row">
        <span class="option-lb">右边框.    </span>
        <input class="option" type="number" @change="changeRight(mright.number,mright.color)"  v-model="mright.number" />
        <input class="option" style="width: 8%;padding: 0px;margin-left: 10px;vertical-align: middle;margin-top: -2px;"  type="color"  @change="changeRight(mright.number,mright.color)" v-model="mright.color" />
    </div>
    <hr>
    <div class="q-row">
        <span class="option-lb">标题字体.    </span>
        <select class="option" v-model="fontSelected" @change="getFontSelected">
            <option v-for="font in fontFamily" :value="font.id"  >{{font.name}}</option>
        </select>
    </div>
    <div class="q-row">
        <span class="option-lb">标题字号.    </span>
        <input class="option" type="number" @blur="changeSize(fontSize)"  v-model="fontSize" />
    </div>
    <div class="q-row">
        <span class="option-lb">对齐方式.    </span>
        <select class="option" v-model="locationSelected" @change="getLocationSelected">
            <option v-for="location in fontLocation" :value="location.id"  >{{location.name}}</option>
        </select>
    </div>
    <div class="q-row">
        <span class="option-lb">是否加粗.    </span>
        <select class="option" v-model="weightSelected" @change="getWeightSelected">
            <option v-for="weight in fontWeight" :value="weight.id"  >{{weight.name}}</option>
        </select>
    </div>
    <hr>
    <div class="q-row">
        <span class="option-lb">题目字体.    </span>
        <select class="option" v-model="fontSelected_com" @change="getFontSelected_com">
            <option v-for="font in fontFamily_com" :value="font.id"  >{{font.name}}</option>
        </select>
    </div>
    <div class="q-row">
        <span class="option-lb">题目字号.    </span>
        <input class="option" type="number" @blur="changeSize_com(fontSize_com)"  v-model="fontSize_com" />
    </div>
    <div class="q-row">
        <span class="option-lb">对齐方式.    </span>
        <select class="option" v-model="locationSelected_com" @change="getLocationSelected_com">
            <option v-for="location in fontLocation_com" :value="location.id"  >{{location.name}}</option>
        </select>
    </div>
    <div class="q-row">
        <span class="option-lb">是否加粗.    </span>
        <select class="option" v-model="weightSelected_com" @change="getWeightSelected_com">
            <option v-for="weight in fontWeight_com" :value="weight.id"  >{{weight.name}}</option>
        </select>
    </div>

</div>
</template>

<script>
    import {eventBus} from "../assets/js/eventBus";

    export default {
        name: "componentsBackground",
        props:["editData"],
        data() {
             return {
                backgroundList:this.$props.editData.backgroundList,
                mtop:this.$props.editData.mtop,
                mbottom:this.$props.editData.mbottom,
                mleft:this.$props.editData.mleft,
                mright:this.$props.editData.mright,
                //mData: this.$props.editData,
                fontFamily:this.$props.editData.fontFamily,
                fontSize:this.$props.editData.fontSize,//标题字号
                fontLocation:this.$props.editData.fontLocation,//标题位置
                fontWeight:this.$props.editData.fontWeight,//标题是否加粗
                fontFamily_com:this.$props.editData.fontFamily_com,
                fontSize_com:this.$props.editData.fontSize_com,//各题目字号
                fontLocation_com:this.$props.editData.fontLocation_com,//各题目位置
                fontWeight_com:  this.$props.editData.fontWeight_com,//各题目是否加粗
             }
         },
        created(){
            for(var i=0;i<this.fontFamily.length;i++){
                if(this.fontFamily[i].pick){
                    this.fontSelected = this.fontFamily[i].id;
                }
            }
            for(var i=0;i<this.fontLocation.length;i++){
                if(this.fontLocation[i].pick){
                    this.locationSelected = this.fontLocation[i].id;
                }
            }
            for(var i=0;i<this.fontWeight.length;i++){
                if(this.fontWeight[i].pick){
                    this.weightSelected = this.fontWeight[i].id;
                }
            }
            for(var i=0;i<this.fontFamily_com.length;i++){
                if(this.fontFamily_com[i].pick){
                    this.fontSelected_com = this.fontFamily_com[i].id;
                }
            }
            for(var i=0;i<this.fontLocation_com.length;i++){
                if(this.fontLocation_com[i].pick){
                    this.locationSelected_com = this.fontLocation_com[i].id;
                }
            }
            for(var i=0;i<this.fontWeight_com.length;i++){
                if(this.fontWeight_com[i].pick){
                    this.weightSelected_com = this.fontWeight_com[i].id;
                }
            }
        },
        mounted(){

        },
        methods: {
            pickup:function(obj){

                for(var i=0;i<this.backgroundList.length;i++){
                    this.backgroundList[i].style1="";
                    this.backgroundList[i].style2="";
                    if(this.backgroundList[i].id1==obj){
                        this.backgroundList[i].style1="border:3px solid #16A3FB;";
                    }else if(this.backgroundList[i].id2==obj){
                        this.backgroundList[i].style2="border:3px solid #16A3FB;";
                    }
                }
                //var backstyle="background: url("+$("#pickup"+obj).find("a").eq(0).find("img").eq(0).attr("src")+") no-repeat center 0";
                var backstyle="background: url("+$("#pickup"+obj).find("a").eq(0).find("img").eq(0).attr("src")+") center center / cover no-repeat fixed;";
                var back=obj;
                if(obj==0){
                    backstyle="";
                    back="";
                }
                let databack={back:back,backstyle:backstyle}
                eventBus.$emit('pickup', databack);
            },
            changeTop:function(obj1,obj2){
                /*eventBus.$emit('changeTop', obj1);
                eventBus.$emit('changeTop1', obj2);*/
                eventBus.$emit("changeTop", {
                    obj1: obj1,
                    obj2: obj2
                })
            },
            /*changeTopCor:function(obj){
                eventBus.$emit('changeTopCor', obj);
            },*/
            changeBottom:function(obj1,obj2){
                //eventBus.$emit('changeBottom', obj);
                eventBus.$emit("changeBottom", {
                    obj1: obj1,
                    obj2: obj2
                })
            },
            /*changeBottomCor:function(obj){
                eventBus.$emit('changeBottomCor', obj);
            },*/
            changeLeft:function(obj1,obj2){
                //eventBus.$emit('changeLeft', obj);
                eventBus.$emit("changeLeft", {
                    obj1: obj1,
                    obj2: obj2
                })
            },
           /* changeLeftCor:function(obj){
                eventBus.$emit('changeLeftCor', obj);
            },*/
            changeRight:function(obj1,obj2){
                //eventBus.$emit('changeRight', obj);
                eventBus.$emit("changeRight", {
                    obj1: obj1,
                    obj2: obj2
                })
            },
           /* changeRightCor:function(obj){
                eventBus.$emit('changeRightCor', obj);
            },*/
            getFontSelected:function(){
                for(var i=0;i<this.fontFamily.length;i++){
                    this.fontFamily[i].pick=false;
                    if(this.fontFamily[i].id==this.fontSelected){
                        this.fontFamily[i].pick=true;
                    }
                }
                var obj=this.fontFamily[this.fontSelected].name;
                eventBus.$emit('changeTitleFont', obj);
            },
            getLocationSelected:function(){
                for(var i=0;i<this.fontLocation.length;i++){
                    this.fontLocation[i].pick=false;
                    if(this.fontLocation[i].id==this.locationSelected){
                        this.fontLocation[i].pick=true;
                    }
                }
                var obj=this.fontLocation[this.locationSelected].inname;
                eventBus.$emit('changeTitleLocation', obj);
            },
            getWeightSelected:function(){
                for(var i=0;i<this.fontWeight.length;i++){
                    this.fontWeight[i].pick=false;
                    if(this.fontWeight[i].id==this.weightSelected){
                        this.fontWeight[i].pick=true;
                    }
                }
                var obj=this.fontWeight[this.weightSelected].inname;
                eventBus.$emit('changeTitleWeight', obj);
            },
            changeSize:function(obj){
                this.fontSize=obj;
                eventBus.$emit('changeTitleSize', obj);
            },
            getFontSelected_com:function(){
                for(var i=0;i<this.fontFamily_com.length;i++){
                    this.fontFamily_com[i].pick=false;
                    if(this.fontFamily_com[i].id==this.fontSelected_com){
                        this.fontFamily_com[i].pick=true;
                    }
                }
                var obj=this.fontFamily_com[this.fontSelected_com].name;
                eventBus.$emit('changeTitleFont_com', obj);
            },
            getLocationSelected_com:function(){
                for(var i=0;i<this.fontLocation_com.length;i++){
                    this.fontLocation_com[i].pick=false;
                    if(this.fontLocation_com[i].id==this.locationSelected_com){
                        this.fontLocation_com[i].pick=true;
                    }
                }
                var obj=this.fontLocation_com[this.locationSelected_com].inname;
                eventBus.$emit('changeTitleLocation_com', obj);
            },
            getWeightSelected_com:function(){
                for(var i=0;i<this.fontWeight_com.length;i++){
                    this.fontWeight_com[i].pick=false;
                    if(this.fontWeight_com[i].id==this.weightSelected_com){
                        this.fontWeight_com[i].pick=true;
                    }
                }
                var obj=this.fontWeight_com[this.weightSelected_com].inname;
                eventBus.$emit('changeTitleWeight_com', obj);
            },
            changeSize_com:function(obj){
                this.fontSize_com=obj;
                eventBus.$emit('changeTitleSize_com', obj);
            },
        }


    }
</script>

